import React, { Component } from 'react';
import { ExerciseService } from "../services/exercise";
import { scaleLinear, select, axisLeft, axisBottom, extent, min } from "d3";
import { Box } from "@material-ui/core";

export class Game extends Component {

  state = {
    exercise: {},
    beta: 0
  }

  formatAngleData(val) {
    return Number(val).toFixed(0);
  }

  createGame = async () => {

    const exercise = await ExerciseService.getExercise();

    this.setState({ exercise });

    const {
      initialRange,
      targetUpperLimit,
      targetLowerLimit
    } = this.state.exercise;

    const warnLine = {
      lineValue: targetUpperLimit,
      label: 'Upper limit'
    }

    const successLine = {
      lineValue: targetLowerLimit,
      label: 'Lower limit'
    }

    const initialRangeLine = {
      lineValue: initialRange,
      label: 'Initial range'
    }

    let margin = { top: 1, right: 1, bottom: 1, left: 1 },
      width = window.innerWidth - margin.left - margin.right, // Use the window's width 
      height = window.innerHeight - margin.top - margin.bottom; // Use the window's height

    const yDomain = extent([targetLowerLimit - 80, targetUpperLimit + 10]);

    const yScale = scaleLinear()
      .domain([targetLowerLimit - 80, targetUpperLimit + 10]) // input 
      .range([height, 0]); // output

    window.yScale = yScale;

    const xScale = scaleLinear()
      .domain([0, 1]) // input
      .range([0, width]); // output

    const svg = select('#game').append("svg")
      .attr("width", width + margin.left + margin.right)
      .attr("height", height + margin.top + margin.bottom)
      .append("g")
      .attr("transform", "translate(" + margin.left + "," + margin.top + ")");

    svg.append("g")
      .attr("class", "y axis")
      .call(axisLeft(yScale)); // Create an axis component with d3.axisLeft

    svg.append("g")
      .attr("class", "x axis")
      .attr("transform", "translate(0," + height + ")")
      .call(axisBottom(xScale)); // Create an axis component with d3.axisBottom

    svg.append('line')
      .attr('x1', xScale(0))
      .attr('y1', yScale(warnLine.lineValue))
      .attr('x2', xScale(1))
      .attr('y2', yScale(warnLine.lineValue))
      .attr('class', 'warn-line');

    svg.append('text')
      .attr('x', xScale(1))
      .attr('y', yScale(warnLine.lineValue))
      .attr('dy', '1em')
      .attr('text-anchor', 'end')
      .text(warnLine.label)
      .attr('class', 'warn-line-text');

    svg.append('line')
      .attr('x1', xScale(0))
      .attr('y1', yScale(successLine.lineValue))
      .attr('x2', xScale(1))
      .attr('y2', yScale(successLine.lineValue))
      .attr('class', 'success-line');

    svg.append('text')
      .attr('x', xScale(1))
      .attr('y', yScale(successLine.lineValue))
      .attr('dy', '1em')
      .attr('text-anchor', 'end')
      .text(successLine.label)
      .attr('class', 'success-line-text');

    svg.append('line')
      .attr('x1', xScale(0))
      .attr('y1', yScale(initialRangeLine.lineValue))
      .attr('x2', xScale(1))
      .attr('y2', yScale(initialRangeLine.lineValue))
      .attr('class', 'initial-line');

    svg.append('text')
      .attr('x', xScale(1))
      .attr('y', yScale(initialRangeLine.lineValue))
      .attr('dy', '1em')
      .attr('text-anchor', 'end')
      .text(initialRangeLine.label)
      .attr('class', 'initial-line-text');

    // Ball
    svg.append('circle')
      .attr('cx', xScale(0.5))
      .attr('cy', yScale(min(yDomain)))
      .attr('r', 30)
      .attr('class', 'circle');

    svg.append('text')
      .attr('x', xScale(0.5))
      .attr('y', yScale(min(yDomain)))
      .text(`${this.state.beta}°`)
      .attr('class', 'ball-beta');

    function updateBall(event) {
      let { beta } = event;
      beta = this.formatAngleData(beta);
      this.setState({ beta });
      select('.circle')
        .attr('cy', yScale(beta));
      select('.ball-beta')
        .text(`${this.state.beta}°`);
    }

    window.addEventListener('deviceorientation', updateBall, true);
  }

  componentDidMount = async () => {
    await this.createGame();
  }

  render() {
    return (
      <Box id="game"></Box>
    )
  }

}